.brickCard {
  margin: 10px;
  cursor: pointer;

  &:hover {

    .imgWrapper {
      box-shadow: 0 0 5px 5px white;

      img {
          transform: scale(1.3);
      }

    }

    p {
      color: lime;
    }

  }

  .imgWrapper {
    width: 220px;
    height: 112px;
    border-radius: 5px;
    overflow: hidden;
    background-color: #FFF;
    transition: 0.2s;
    display: flex;
    align-items: center;
    position: relative;

    img {
      display: block;
      width: 100%;
      height: auto;
      transition: 0.2s;
    }

    .skeleton {
      position: absolute;
      top: 0;
      left: 0;
    }

  }

  p {
    color: orange;
    text-align: center;
    margin-top: 5px;
    transition: 0.2s;
  }

}