@import "../../assets/scss/app.scss";

.bricks {
  height: 100%;

  .inner {
    background-color: rgba(33, 35, 31, 0.75);
    max-width: 1120px;
    width: 100%;
    height: 100%;
    padding: 20px;
    margin: 0 auto;
    box-sizing: border-box;

    .titleBlock {
      display: grid;
      grid-template-columns: 1fr auto 1fr;
      justify-content: center;
      align-items: center;

      .title {
        @include h1();
        text-align: center;
        padding: 0px 20px;
        width: 340px;
      }

      .left {
        display: flex;
        justify-content: right;
      }

      .right {

      }

      img {
        display: block;
        max-height: 100px;
        height: 100%;
        //width: 300px;
        //min-width: 150px;
      }
    }

    .sortMethod {
      margin-top: 20px;
      display: flex;
      align-items: center;

      p {
        color: #FFF;
      }

      .links {
        display: flex;

        a {
          text-decoration: none;

          .link {
            margin-left: 30px;
            font-size: 18px;
            color: gold;
            transition: 0.3s;

            &:hover {
              color: greenyellow;
            }


            &_active {
              color: greenyellow;
              text-decoration: underline;
            }
          }
        }


      }
    }

    .outlet {
      margin-top: 20px;
    }

  }
}