.bySearch {
  display: flex;

  .filters {
    align-self: stretch;
    width: 200px;
    flex-shrink: 0;
    border-right: 1px solid rgba(255,255,255, 0.4);
    padding-right: 10px;
    box-sizing: border-box;

    .filter {
      &:not(:first-child) {
        border-top: 1px solid rgba(255,255,255, 0.4);
      }
    }
  }

  .content {
    flex-grow: 1;
  }
}