
.personalData {
  display: flex;

  .avatarBlock {
    display: flex;
    flex-direction: column;
    align-items: center;

    .avatarWrapper {
      width: 140px;
      height: 140px;
      border-radius: 50%;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      //border: 1px solid black;

      img {
        display: block;
        width: 140px;
        height: 140px;
        object-fit: cover;
        object-position: center;
      }
    }
  }

  .nicknameBlock {
    flex-grow: 1;
    margin-left: 20px;
  }

}