.chronoEditItem {
  margin-top: 10px;

  .header {
    display: flex;
    align-items: center;

    .text {

      flex-grow: 1;
      height: 32px;
      padding: 0 10px;
      display: flex;
      align-items: center;
      box-sizing: border-box;
      border: 1px solid #777;
      border-radius: 5px;

      p {
        width: calc(100vw - 100px - 245px);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        span:first-child {
          font-weight: bold;
        }

        span:last-child {
          font-style: italic;
          color: #444;
        }
      }
    }

    .btn {
      margin-left: 10px;
      text-transform: none;
      flex-shrink: 0;
      box-sizing: border-box;
    }

    .btnEdit {
      width: 145px;
    }

    .btnDelete {
      width: 100px;
    }


  }
}