@import "../../assets/scss/app.scss";

.chrono {
  height: calc(100vh - 60px);

  .innerScroll {
    height: calc(100vh - 60px);
    overflow-y: scroll;

    .inner {
      max-width: 1120px;
      width: 100%;
      padding: 20px;
      margin: 0 auto;
      box-sizing: border-box;
      background-color: rgba(33, 35, 31, 0.75);

      .title {
        @include h1();
        text-align: center;
      }

      .items {
        margin-top: 20px;
      }
    }

  }
}