@import "../../../assets/scss/app.scss";

.byLocation {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .link {
    text-decoration: none;
    transition: 0.3s;
    margin: 5px;

    &:hover {
      .imgWrapper {
        border: 10px solid orange;
        transform: scale(1.02);
      }
      p {
        color: orange;
        //font-weight: bold;
      }
    }

    .imgWrapper {
      width: 180px;
      height: 180px;
      background: #000;
      border-radius: 20px;
      border: 10px solid #FFF;
      box-sizing: border-box;
      transition: 0.3s;
      padding: 10px;
      @include center();

      img {
        height: 100%;
        width: 100%;
      }


    }

    p {
      max-width: 180px;
      margin-top: 5px;
      font-size: 24px;
      text-align: center;
      transition: 0.3s;
      color: #FFF;
    }


  }
}