.registrationPage {
  min-height: calc(100vh - 60px);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  box-sizing: border-box;
  background: rgba(255,255,255, 0.8);

  .form {
    width: 600px;

    .field {
      margin-top: 30px;
    }

  }
}