@import "../../assets/scss/app.scss";

.book {
  height: calc(100vh - 60px);

  .innerScroll {
    height: calc(100vh - 60px);
    overflow-y: scroll;

    .inner {
      max-width: 1120px;
      width: 100%;
      padding: 20px;
      margin: 0 auto;
      box-sizing: border-box;
      background-color: rgba(33, 35, 31, 0.75);

      .title {
        @include h1();
        text-align: center;
      }

      .sortBlock {
        margin-top: 5px;
        padding: 020px;

        display: flex;
        justify-content: space-between;
        align-items: center;

        .text {
          color: #FFF;
        }

        .sortButtons {
          display: flex;

          .btn {
            cursor: pointer;
            color: #FFF;
            transition: 0.3s;
            user-select: none;

            &:hover {
              color: lime;
            }

            &_selected {
              color: lime;
              text-decoration: underline;
            }


            &:not(:first-child) {
              margin-left: 20px;
            }
          }
        }

      }

      .items {
        margin-top: 5px;

        .item {
          &:not(:first-child) {
            margin-top: 10px;
          }
        }
      }
    }

  }
}