.item {
  cursor: pointer;
  display: flex;
  transition: 0.3s;

  &:not(:first-child) {
    margin-top: 10px;
  }

  .date {
    flex-shrink: 0;
    background: #FFF;
    border-radius: 10px;
    width: 150px;
    height: 50px;
    padding: 0 10px;
    display: flex;
    align-items: center;
    transition: 0.3s;
    font-size: 16px;
    user-select: none;
  }

  .imgWrapper {
    flex-shrink: 0;
    margin-left: 10px;
    height: 50px;
    width: 50px;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    transition: 0.3s;

    .img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      //border-radius: 10px;
      transition: 0.3s;
    }

    .skeleton {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }


  }

  .textBlock {
    flex-grow: 1;
    margin-left: 10px;
    background: #FFF;
    border-radius: 10px;
    height: 50px;
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: 0.3s;

    p {
      user-select: none;
    }

    .text {
      font-size: 16px;
    }

    .fullText {
      font-size: 14px;
      margin-top: 10px;
      color: #555;
    }
  }

  &_selected {
    .date {
      height: 200px;
      color: darkgreen;
      font-size: 18px;
    }

    .imgWrapper {
      height: 200px;
      width: 200px;
    }

    .textBlock {
      height: 200px;
    }
  }

}