.byMark {

  .links {
    display: flex;
    flex-wrap: wrap;

    .link {
      text-decoration: none;
      &:hover {
        .linkInner {
          background: brown;
        }

      }

      .linkInner {
        padding: 5px 10px;
        background: indianred;
        margin: 5px;
        border-radius: 3px;
        transition: 0.3s;
        white-space: nowrap;
        color: #FFF;

        &_active {
          background: brown;
          color: lime;
        }

      }

    }

  }

  .outlet {
    margin-top: 20px;
  }

}