@import "../../assets/scss/app.scss";

.home {
  height: 100%;

  .inner {
    background-color: rgba(33, 35, 31, 0.75);
    max-width: 1120px;
    width: 100%;
    height: 100%;
    padding: 10px;
    margin: 0 auto;
    box-sizing: border-box;

    .logo {
      @include onlyDesktop() {
        display: block;
        margin: 0 auto;
        max-width: 280px;
        //height: auto;
      }

    }

    .links {
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      .link {
        margin: 10px;
        text-decoration: none;

        &:hover {
          img {
            border: 10px solid orange;
            transform: scale(1.03);
          }

          p {
            color: #21231f;
            font-weight: bold;

            &:before {
              transform: translateX(0%);
            }
          }
        }

        img {
          max-width: 200px;
          border-radius: 25px;
          border: 10px solid transparent;
          transition: .2s;
        }

        p {
          color: gold;
          font-size: 18px;
          text-align: center;
          position: relative;
          z-index: 2;
          transition: color 0.2s;
          overflow: hidden;

          &::before {
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            content: "";
            display: block;
            background: gold;
            transform: translateX(-105%);
            transition: 0.2s;
          }
        }
      }
    }

  }
}