.bookItem {
  cursor: pointer;
  display: flex;

  .imgBlock {
    width: 100px;
    height: 100px;
    background: #21231f;
    border-radius: 10px;
    transition: 0.3s;

    .imgWrapper {
      width: 100px;
      height: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.3s;

      img {
        display: block;
        height: 100%;
        width: auto;
        transition: 0.3s;
      }

      &_horizontal img {
        width: 100%;
        height: auto;
      }

    }

    .slider {
      display: none;
      position: relative;
      z-index: 1;
      width: 100px;
      height: 100px;
      transition: 0.3s;
      position: relative;

      .slide {
        width: 100px;
        height: 100px;
        transition: 0.3s;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          display: block;
          height: 100%;
          width: auto;
        }

        &_horizontal img {
          width: 100%;
          height: auto;
        }
      }

      .btn {
        position: absolute;
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);

        &_left {
          left: 0;
        }

        &_right {
          right: 0;
        }
      }
    }

  }

  .textBlock {
    margin-left: 10px;
    flex-grow: 1;
    background: #FFF;
    border-radius: 10px;
    padding: 20px;
    box-sizing: border-box;

    .author {
      color: #444444;
      font-style: italic;
    }

    .title {
      //margin-top: 5px;
      font-weight: bold;
    }

    .info {
      //margin-top: 5px;
      color: #444444;
    }

  }

  &_selected {

    .imgBlock {
      width: 500px;
      height: 500px;

      .imgWrapper {
        width: 500px;
        height: 500px;
        display: none;
      }

      .slider {
        width: 500px;
        height: 500px;
        display: block;

        .slide {
          width: 500px;
          height: 500px;
        }
      }
    }

    .textBlock {
      .title {
        margin-top: 5px;
      }

      .info {
        margin-top: 5px;
      }
    }

  }
}