@mixin desktop {
  @media all and (min-width: 1024px) {
    @content;
  }
}

@mixin onlyMobile($displayType: block) {
  display: $displayType;
  @content;

  @include desktop() {
    display: none;
  }
}

@mixin onlyDesktop($displayType: block) {
  display: none;

  @include desktop {
    display: $displayType;
    @content;
  }
}

@mixin center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@mixin button {
  border: none;
  background: none;
  cursor: pointer;
}

@mixin h1() {
  font-size: 36px;
  color: orange;
}

