.markCard {
  padding: 30px 20px 20px;
  background: rgba(22, 23, 21, 0.5);
  border: 1px solid #FFF;
  border-radius: 10px;
  position: relative;

  .name {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #42463e;
    border: 2px solid #FFF;
    padding: 2px 15px;
    font-size: 24px;
    color: orange;
  }

  .description {
    color: #FFF;
  }

  .brickCards {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
}